html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  color: rgba(44, 46, 63, 0.7);
  font-size: 16px;
  line-height: 24px;
  font-weight: 400; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  max-width: 1920px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 768px; } }
  @media (max-width: 767.9px) {
    .container {
      width: 320px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  min-height: 99px;
  padding: 0 19.532%;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 1023px) {
    .page-header {
      min-height: 50px;
      padding: 0 2.75%;
      position: relative; } }

.page-header__list {
