  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  margin: 0 40px;
  display: flex; }
  @media (max-width: 1023px) {
    .page-header__list {
      background-color: #ffffff;
