      padding-bottom: 284px; } }

.page-header__item:not(:last-child) {
  margin-right: 30px; }
  @media (max-width: 767.9px) {
    .page-header__item:not(:last-child) {
      margin-right: 0; } }

.page-header__link {
  color: #141517;
  padding: 40px 10px;
  position: relative; }
  @media (max-width: 1023px) {
    .page-header__link {
      padding: 16px;
      padding-right: 30px;
      display: block; } }
  @media (max-width: 1023px) {
    .page-header__link::before {
      position: absolute;
      content: "";
      width: 14px;
      height: 14px;
      top: 15px;
      right: 0;
