      background-size: cover;
      background-repeat: no-repeat; } }
  @media (max-width: 767.9px) {
    .page-header__link::before {
      width: 25px;
      height: 25px;
      right: 16px;
      background-image: url("../img/icons/icon-arrow-right.svg"); } }

.page-header__tel {
  display: block;
  font-size: 18px;
  line-height: 32px;
  color: #FE7865;
  text-decoration-line: underline; }
  @media (max-width: 1023px) {
    .page-header__tel {
      display: none; } }

.page-header__nav {
  margin-left: auto; }
  @media (max-width: 1023px) {
    .page-header__nav {
      display: none; } }

.page-header__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      margin: 0;
      padding: 0;
      border: 0;
      border-radius: 0;
      color: inherit;
      font: inherit;
      background-color: transparent;
      position: absolute;
      top: 0;
      right: 0;
      width: 72px;
      height: 50px;
      cursor: pointer;
      background-color: #FE7865;
      z-index: 2; } }

.page-header__toggle {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  color: inherit;
  font: inherit;
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 0;
  width: 72px;
  height: 50px;
  cursor: pointer;
  background-color: #FE7865; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      display: block; } }

.page-header__toggle-line {
  position: absolute;
  top: 23px;
  right: 23px;
  width: 23px;
  height: 3px;
  font-size: 0;
  background-color: #ffffff; }
  .page-header__toggle-line::after, .page-header__toggle-line::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 23px;
    height: 3px;
    background-color: #ffffff;
    transform-origin: 50% 50%;
    transition: top 0.3s 0.3s, transform 0.3s; }
  .page-header__toggle-line::after {
    top: 7px; }
  .page-header__toggle-line::before {
    top: -7px; }

@media (max-width: 1023px) {
  .page-header--nojs .page-header__toggle {
    display: none; } }

@media (max-width: 1023px) {
  .page-header--nojs .page-header__nav {
    display: block; } }

.page-header--opened .page-header__nav {
  display: block; }

.page-header--opened .page-header__toggle-line {
  background-color: #FE7865; }
  .page-header--opened .page-header__toggle-line::after, .page-header--opened .page-header__toggle-line::before {
    top: 0;
    background-color: #ffffff;
    transition: top 0.3s, transform 0.3s  0.3s; }
  .page-header--opened .page-header__toggle-line::after {
    transform: rotate(45deg); }
  .page-header--opened .page-header__toggle-line::before {
    transform: rotate(-45deg); }

